$hover-opacity: 0.08;
$ripple-focus-opacity: 0.12;

@mixin toggle() {
    $rippleSize: 0.5rem;
    $track-w: 34px;
    $track-h: 14px;
    $track-center: calc(50% - 7px);
    $thumb-center: calc(50% - 10px);

    .toggle-label {
        display: inline-flex;
        align-items: center;
        line-height: 0;
        user-select: none;
        cursor: pointer;

        @include marginEnd(0.5rem);
    }

    .toggle {
        position: relative;
        overflow: hidden;
        padding: $rippleSize;
        border-radius: 50%;
        font-size: 24px;

        input {
            top: 0;
            left: 0;
            width: 100%;
            cursor: inherit;
            height: 100%;
            margin: 0;
            opacity: 0;
            padding: 0;
            z-index: 1;
            position: absolute;

            & + span + span,
            &:checked + span {
                display: none;
            }

            & + span,
            &:checked + span + span {
                display: inline-block;
            }
        }

        svg {
            font-size: inherit;
        }

        &.dense {
            margin-top: -2px;
            margin-bottom: -2px;

            @include marginEnd(-4px);
        }

        &.edge-start {
            @include marginStart(-$rippleSize);
        }

        &.edge-end {
            @include marginEnd(-$rippleSize);
        }

        .ripple-base .ripple-focus {
            opacity: $ripple-focus-opacity;
        }
    }

    .toggle-sm {
        font-size: 20px;
    }

    .toggle-lg {
        font-size: 32px;
    }

    .toggle-xl {
        font-size: 45px;
    }

    .toggle.disabled {
        opacity: 0.4;
        cursor: default;

        &:hover {
            background-color: transparent !important;
        }
    }
}

@mixin toggleTheme($palette) {
    $text: map-get($palette, text);
    $bg: map-get($palette, bg);

    $primary-palette: map-get($palette, primary);
    $primary: map-get($primary-palette, main);

    $secondary-palette: map-get($palette, secondary);
    $secondary: map-get($secondary-palette, main);

    .toggle {
        color: mix($text, $bg, 75);

        span:nth-child(2) {
            color: mix($text, $bg, 75) !important;
        }

        &:hover {
            background-color: rgba($text, $hover-opacity);
        }
    }

    .toggle-primary {
        color: $primary;

        &:hover {
            background-color: rgba($primary, $hover-opacity);
        }
    }

    .toggle-secondary {
        color: $secondary;

        &:hover {
            background-color: rgba($secondary, $hover-opacity);
        }
    }
}

@include toggle();

.theme-mui.theme-light {
    $palette: map-get($theme, light);
    @include toggleTheme($palette);
}

.theme-mui.theme-dark {
    $palette: map-get($theme, dark);
    @include toggleTheme($palette);
}
