@mixin textAlignStart() {
    [dir="rtl"] &,
    .rtl & {
        text-align: right;
    }

    [dir="ltr"] &,
    .ltr & {
        text-align: left;
    }
}

@mixin textAlignEnd() {
    [dir="rtl"] &,
    .rtl & {
        text-align: left;
    }

    [dir="ltr"] &,
    .ltr & {
        text-align: right;
    }
}

@mixin paddingStart($value) {
    [dir="rtl"] &,
    .rtl & {
        padding-right: $value;
    }

    [dir="ltr"] &,
    .ltr & {
        padding-left: $value;
    }
}

@mixin paddingEnd($value) {
    [dir="rtl"] &,
    .rtl & {
        padding-left: $value;
    }

    [dir="ltr"] &,
    .ltr & {
        padding-right: $value;
    }
}

@mixin marginStart($value) {
    [dir="rtl"] &,
    .rtl & {
        margin-right: $value;
    }

    [dir="ltr"] &,
    .ltr & {
        margin-left: $value;
    }
}

@mixin marginEnd($value) {
    [dir="rtl"] &,
    .rtl & {
        margin-left: $value;
    }

    [dir="ltr"] &,
    .ltr & {
        margin-right: $value;
    }
}

@mixin positionStart($value) {
    [dir="rtl"] &,
    .rtl & {
        right: $value;
    }

    [dir="ltr"] &,
    .ltr & {
        left: $value;
    }
}

@mixin positionEnd($value) {
    [dir="rtl"] &,
    .rtl & {
        left: $value;
    }

    [dir="ltr"] &,
    .ltr & {
        right: $value;
    }
}

//
@mixin borderStart($value) {
    [dir="rtl"] &,
    .rtl & {
        border-right: $value;
    }

    [dir="ltr"] &,
    .ltr & {
        border-left: $value;
    }
}

@mixin borderEnd($value) {
    [dir="rtl"] &,
    .rtl & {
        border-left: $value;
    }

    [dir="ltr"] &,
    .ltr & {
        border-right: $value;
    }
}

//
@mixin create-classes($selectors, $property) {
    #{$selectors}-0 {
        #{$property}: 0 !important;
    }

    #{$selectors}-1 {
        #{$property}: 0.25rem !important;
    }

    #{$selectors}-2 {
        #{$property}: 0.5rem !important;
    }

    #{$selectors}-3 {
        #{$property}: 1rem !important;
    }

    #{$selectors}-4 {
        #{$property}: 1.5rem !important;
    }

    #{$selectors}-5 {
        #{$property}: 3rem !important;
    }
}

@mixin create-n-classes($selectors, $property) {
    #{$selectors}-n1 {
        #{$property}: -0.25rem !important;
    }

    #{$selectors}-n2 {
        #{$property}: -0.5rem !important;
    }

    #{$selectors}-n3 {
        #{$property}: -1rem !important;
    }

    #{$selectors}-n4 {
        #{$property}: -1.5rem !important;
    }

    #{$selectors}-n5 {
        #{$property}: -3rem !important;
    }
}

@mixin start-end-classes($start-prop, $end-prop) {
    @include create-classes(".p-s", "padding-" + $start-prop);
    @include create-classes(".p-e", "padding-" + $end-prop);
    @include create-classes(".m-s", "margin-" + $start-prop);
    @include create-classes(".m-e", "margin-" + $end-prop);
    @include create-n-classes(".m-s", "margin-" + $start-prop);
    @include create-n-classes(".m-e", "margin-" + $end-prop);
}

@mixin rtl-support() {
    .ltr,
    [dir="ltr"] {
        @include start-end-classes("left", "right");
    }

    .rtl,
    [dir="rtl"] {
        @include start-end-classes("right", "left");
    }

    [dir="rtl"],
    .rtl {
        direction: rtl;
        text-align: right;

        .ltr {
            direction: ltr;
            text-align: left;
        }

        .form-select {
            background-position: left 0.75rem center;
            padding: 0.375rem 0.75rem 0.375rem 2.25rem;
        }

        .form-check .form-check-input {
            float: right;
            margin-left: 0.5em;
        }

        .text-start {
            text-align: right !important;
        }

        .text-end {
            text-align: left !important;
        }
    }

    .ltr {
        direction: ltr;
        text-align: left;

        .rtl {
            direction: rtl;
            text-align: right;
        }
    }

    /* floating label */
    .rtl,
    [dir="rtl"] {
        .form-floating > label {
            transform-origin: top right;
            left: auto;
        }

        .form-floating > .form-control:focus ~ label,
        .form-floating > .form-control:not(:placeholder-shown) ~ label,
        .form-floating > .form-select:focus ~ label,
        .form-floating > .form-select:not(:placeholder-shown) ~ label {
            transform: scale(0.85) translateY(-0.45rem) translateX(-0.15rem);
        }
    }

    /* MODAL */
    .modal-header {
        [dir="rtl"] & .btn-close,
        .rtl & .btn-close {
            margin: -0.5rem auto -0.5rem -0.5rem;
        }
    }

    /* list-group */
    .list-group {
        padding: 0;
    }

    /* input-group */
    [dir="rtl"],
    .rtl {
        .input-group:not(.has-validation) > :not(:last-child):not(.dropdown-toggle):not(.dropdown-menu),
        .input-group:not(.has-validation) > .dropdown-toggle:nth-last-child(n + 3) {
            border-top-right-radius: 0.25rem;
            border-bottom-right-radius: 0.25rem;
            border-top-left-radius: 0;
            border-bottom-left-radius: 0;
        }

        .input-group > :not(:first-child):not(.dropdown-menu):not(.valid-tooltip):not(.valid-feedback):not(.invalid-tooltip):not(.invalid-feedback) {
            margin-left: 0;
            margin-right: -1px;
            border-top-right-radius: 0;
            border-bottom-right-radius: 0;
            border-top-left-radius: 0.25rem;
            border-bottom-left-radius: 0.25rem;
        }
    }
    /* offcanvas */
    .rtl,
    [dir="rtl"] {
        .offcanvas-header .btn-close {
            margin: -0.5rem auto -0.5rem -0.5rem;
        }
    }
}
