@mixin paper() {
    .paper-rounded {
        border-radius: 4px;
    }
}

@mixin paperTheme($palette) {
    $text: map-get($palette, text);
    $bg: map-get($palette, bg);
    $bg: light-color($bg, 0.15);

    .paper {
        color: $text;
        background-color: $bg;
    }

    .paper-outline {
        border: 1px solid rgba($text, 0.18);
    }
}

@include paper();

.theme-mui.theme-light {
    @include paperTheme(map-get($theme, light));
}

.theme-mui.theme-dark {
    @include paperTheme(map-get($theme, dark));
}
