.scroll-pane-container {
    flex-grow: 1;
    display: flex;
    height: max-content;
    overflow-y: hidden;
    position: relative;
    width: 100%;
    flex-wrap: nowrap;
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */

    &::-webkit-scrollbar {
        display: none;
    }
}

.scroll-pane-vertical {
    flex-direction: column;

    .scroll-pane-container {
        flex-direction: column;
        overflow-y: auto;
        overflow-x: hidden;
    }
}
