.dropdown-item,
.dropdown-item > .dropdown-toggle {
    display: flex;
    align-items: center;
}

.dropdown-submenu {
    padding: 0;

    & > .dropdown-item {
        position: relative;

        &:after {
            position: absolute;

            .ltr &,
            [dir="ltr"] & {
                right: 16px;

                border-top: 0.3em solid transparent;
                border-bottom: 0.3em solid transparent;
                border-left: 0.3em solid;
                border-right: 0;
            }

            .rtl &,
            [dir="rtl"] & {
                left: 16px;

                border-top: 0.3em solid transparent;
                border-bottom: 0.3em solid transparent;
                border-left: 0;
                border-right: 0.3em solid;
            }
        }
    }

    .dropdown-menu {
        .ltr &,
        [dir="ltr"] & {
            margin-left: 10px;
        }

        .rtl &,
        [dir="rtl"] & {
            margin-left: -10px;
        }
    }
}
