.avatar {
    width: 40px;
    height: 40px;
    overflow: hidden;
    position: relative;
    font-size: 1.25rem;
    line-height: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    user-select: none;
    border-radius: 50%;
    color: #fff;
    background-color: #bdbdbd;

    img {
        color: transparent;
        width: 100%;
        height: 100%;
        object-fit: cover;
        text-align: center;
        text-indent: 10000px;
    }

    &.avatar-rounded {
        border-radius: 4px;
    }

    &.avatar-square {
        border-radius: 0;
    }

    &.avatar-sm {
        font-size: 0.75rem;
        width: 24px;
        height: 24px;
    }

    &.avatar-lg {
        font-size: 1.7rem;
        width: 56px;
        height: 56px;
    }
}

.avatar-anim {
    animation: ripple 1.2s infinite ease-in-out;
}

.avatar-group {
    display: flex;
    align-items: center;

    .avatar {
        margin-left: -8px;

        .theme-mui.theme-light & {
            border: 2px solid #fff;
        }

        .theme-mui.theme-dark & {
            border: 2px solid #121212;
        }
    }
}
