/*******************/
@mixin panelTheme($palette) {
    $text: map-get($palette, text);
    $bg: map-get($palette, bg);

    .bd-panel {
        .toolbar {
            &.disabled {
                color: rgba($text, 0.4);
                cursor: default;
                user-select: none;

                &:hover {
                    background-color: transparent;
                }
            }
        }
    }

    .bd-panel-menu {
        .toolbar {
            &:hover {
                background-color: rgba($text, 0.03);
            }
        }
    }
}

/*******************/
.theme-mui.theme-light {
    $palette: map-get($theme, light);
    @include panelTheme($palette);
}

.theme-mui.theme-dark {
    $palette: map-get($theme, dark);
    @include panelTheme($palette);
}
