.badge-root {
    position: relative;
    display: inline-flex;
    flex-shrink: 0;
    vertical-align: middle;

    .badge {
        height: 20px;
        display: flex;
        padding: 0 6px;
        z-index: 1;
        position: absolute;
        flex-wrap: wrap;
        font-size: 0.75rem;
        min-width: 20px;
        box-sizing: border-box;
        transition: transform 225ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
        align-items: center;
        font-family: "Roboto", "Helvetica", "Arial", sans-serif;
        font-weight: 500;
        line-height: 1;
        align-content: center;
        border-radius: 10px;
        flex-direction: row;
        justify-content: center;
        direction: ltr;

        &.badge-top-left {
            top: 0;
            left: 0;
            transform: scale(1) translate(-50%, -50%);
            transform-origin: 0% 0%;

            &.badge-overlap-circle {
                top: 14%;
                left: 14%;
            }
        }

        &.badge-top-right {
            top: 0;
            right: 0;
            transform: scale(1) translate(50%, -50%);
            transform-origin: 100% 0%;

            &.badge-overlap-circle {
                top: 14%;
                right: 14%;
            }
        }

        &.badge-bottom-left {
            left: 0;
            bottom: 0;
            transform: scale(1) translate(-50%, 50%);
            transform-origin: 0% 100%;

            &.badge-overlap-circle {
                bottom: 14%;
                left: 14%;
            }
        }

        &.badge-bottom-right {
            right: 0;
            bottom: 0;
            transform: scale(1) translate(50%, 50%);
            transform-origin: 100% 100%;

            &.badge-overlap-circle {
                bottom: 14%;
                right: 14%;
            }
        }

        &.badge-dot {
            height: 8px;
            padding: 0;
            min-width: 8px;
            border-radius: 4px;
        }

        &.badge-anim {
            &:after {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                border-radius: 50%;
                animation: badge-ripple 1.2s infinite ease-in-out;
                border: 2px solid currentColor;
                content: "";
            }
        }
    }
}

@keyframes badge-ripple {
    0% {
        transform: scale(0.8);
        opacity: 1;
    }

    100% {
        transform: scale(2.4);
        opacity: 0;
    }
}
