.middle,
.container-middle {
    display: flex;
    align-items: center;
    justify-content: center;
}

.cur-pointer {
    cursor: pointer;
}

.animation-delay--1s {
    animation-delay: 0.1s;
}

.animation-delay--2s {
    animation-delay: 0.2s;
}

.animation-delay--3s {
    animation-delay: 0.3s;
}

/* TRANSITION */
.transition {
    transition: all 225ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
}

.transition-transform {
    transition: transform 225ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
}

/* ROTATE */
.rotate-90 {
    transform: rotate(90deg);
}

.rotate-n90 {
    transform: rotate(-90deg);
}

.rotate-180 {
    transform: rotate(180deg);
}

.rotate-n180 {
    transform: rotate(-180deg);
}

.rotate-270 {
    transform: rotate(270deg);
}

.rtl-rotate-180 {
    [dir="rtl"] &,
    .rtl & {
        transform: rotate(180deg);
    }
}

.rtl-rotate-n180 {
    [dir="rtl"] &,
    .rtl & {
        transform: rotate(-180deg);
    }
}

/* */
@mixin border($palette) {
    $text: map-get($palette, text);
    $bg: map-get($palette, bg);

    $primary-palette: map-get($palette, primary);
    $primary: map-get($primary-palette, main);
    $primary-text: map-get($primary-palette, text);

    $secondary-palette: map-get($palette, secondary);
    $secondary: map-get($secondary-palette, main);
    $secondary-text: map-get($secondary-palette, text);

    .border,
    .border-top,
    .border-bottom,
    .border-left,
    .border-right {
        border-color: rgba($text, 0.15) !important;
    }

    .border-primary {
        border-color: $primary !important;
    }

    .border-secondary {
        border-color: $secondary !important;
    }
}

.theme-mui.theme-light {
    @include border(map-get($theme, light));
}

.theme-mui.theme-dark {
    @include border(map-get($theme, dark));
}

/*************
  S I Z E S
*************/

// XL

.size-xl {
    font-size: 1.5rem;
}

svg.size-xl,
.size-xl svg {
    font-size: 2.1875rem;
}

// LG

.size-lg {
    font-size: 1rem;
}

svg.size-lg,
.size-lg svg {
    font-size: 1.8rem;
}

// MD

.size-md {
    font-size: 0.875rem;
}

svg.size-md,
.size-md svg {
    font-size: 1.25rem;
}

// SM

.size-sm {
    font-size: 0.8125rem;
}
svg.size-sm,
.size-sm svg {
    font-size: 1.125rem;
}
